const { google } = require('googleapis');
const FormData = require('form-data');
const axios = require('axios');

export async function uploadFormDataToDrive(formData, accessToken) {
  try {
    // If you have the file as Buffer from FormData
    const fileBuffer = formData; // Assuming you extract the file buffer
    
    const auth = new google.auth.OAuth2();
    auth.setCredentials({ access_token: accessToken });

    const drive = google.drive({ version: 'v3', auth });

    const response = await drive.files.create({
      requestBody: {
        name: `audio-${Date.now()}.webm`,
        mimeType: 'audio/webm',
        parents: ['root'] // Optional: specify folder ID
      },
      media: {
        mimeType: 'audio/webm',
        body: fileBuffer
      },
      fields: 'id, name, webViewLink, webContentLink'
    });

    return {
      success: true,
      fileId: response.data.id,
      fileName: response.data.name,
      viewLink: response.data.webViewLink,
      downloadLink: response.data.webContentLink
    };
  } catch (error) {
    console.error('Google Drive upload error:', error);
    throw error;
  }
}